@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top-header{padding:1.5rem 0;background-color:#2b2b31;position:fixed;top:0;width:100%;z-index:1;box-shadow:0 4px 16px 0 rgba(0,0,0,0.25)}.top-header-logo h2{font-family:'Noto Sans JP', sans-serif;text-transform:uppercase;font-size:2.5rem;font-weight:700;color:#e50914;margin-bottom:0}.top-header-input .form-control{color:#fff;background-color:rgba(255,255,255,0.15);border-color:rgba(255,255,255,0.15);border-radius:20px}.top-header-input .form-control:focus{outline:none;box-shadow:none}.top-header-input input::-webkit-input-placeholder{color:#ddd;opacity:1}.top-header-input input:-ms-input-placeholder{color:#ddd;opacity:1}.top-header-input input::-ms-input-placeholder{color:#ddd;opacity:1}.top-header-input input::placeholder{color:#ddd;opacity:1}.movie-card{margin-bottom:2rem;position:relative;top:0;transition:top ease 0.5s;cursor:pointer}.movie-card:hover{box-shadow:0 4px 16px 0 rgba(0,0,0,0.08);top:-12px}.movie-card .card{border-radius:0}.movie-card .card img{border-radius:0}.movie-card .card-body{padding:1rem}.movie-card .card-body svg{color:#e50914;margin-right:0.5rem}.movie-card .card-body h5{font-family:'Noto Sans JP', sans-serif;font-weight:600}.movie-card .card-text{font-family:'Noto Sans JP', sans-serif;font-weight:300}.movie-card .btn-card{background-color:#e50914;border-radius:0;color:#fff}.movie-card .btn-card:focus,.movie-card .btn-card:hover{background-color:#f8434c;box-shadow:none;outline:none;color:#fff}

